@import "vars";
@import "libs";
@import "fonts";
@import "global";

.header{
  position: fixed;
  left: 0;
  top: 30px;
  right: 0;
  z-index: 10;
  background-color: rgba(0, 0, 0, 0.3);
  padding: 25px 15px;
  &__inner{
    display: flex;
    justify-content: space-between;
    align-items: center;
    min-height: 30px;
    text-transform: uppercase;
    font-size: 20px;
    line-height: 17px;
    @extend %ubuntu-medium;
  }
  &__btn{
    color: #fff;
    background-color: transparent;
    padding: 6px 28px;
    border: 1px solid #FFFFFF;
    box-sizing: border-box;
    border-radius: 3px;
    margin-right: 8px;
    text-transform: uppercase;
    cursor: pointer;
    transition: all .3s;
    &:hover {
      background-color: #fff;
      color: #1C1C1C;
    }
  }
  &__phone{
    @extend %ubuntu-bold;
    font-size: 16px;
    line-height: 18px;
  }
}

.menu{
  &__list-item{
    display: inline-block;
    & + .menu__list-item{
      padding-left: 20px;
    }
  }
  &__list-phone{
    display: none;
  }
  &__btn{
    display: none;
  }
}

.top{
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  position: relative;
  // &::before{
  //   content: '';
  //   position: absolute;
  //   left: 0;
  //   top: 0;
  //   right: 0;
  //   bottom: 0;
  //   background: radial-gradient(45.97% 54.43% at 50% 45.57%, rgba(23, 26, 28, 0) 50.28%, rgba(23, 26, 28, 0.00225974) 50.29%, rgba(4, 5, 5, 0.820343) 58.72%, rgba(5, 5, 6, 0.792703) 80.08%, #000000 87.89%);
  //   opacity: 0.4;
  // }
  &__wrapper{
    display: flex;
    align-items: flex-end;
  }
  &__inner{
    display: flex;
    flex-direction: column;
    text-align: center;
    align-items: center;
    margin-bottom: 70px;
  }
  &__title{
    font-size: 96px;
    line-height: 115px;
    @extend %sf-semibold;
    margin-bottom: 100px;
  }
  &__text{
    margin: 25px 0 35px;
    max-width: 325px;
  }
  &__social-item{
    display: inline-block;
    margin: 0 25px;
  }
  &__social-link{
    opacity: 0.8;
    transition: all .3s;
    &:hover {
      opacity: 1;
    }
  }
  &__social-link svg{
    padding-top: 5px;
    &:hover {
      opacity: 1;
    }
  }
}

.about__slider{
  height: 100vh;
  width: 100%;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: bottom center;
  margin: 0;
  padding: 0;
  z-index: -1;
}

.about__slider-item{
  width: 100%;
  margin: 0;
  padding: 0;
  z-index: -1;
}

.about__slider-item img{
  width: 100%;
  margin: 0;
  padding: 0;
  z-index: -1;
}

.slick-slider button{
  display: none;
}

.surf-slider {
  padding-top: 20px;

  .slider-arrows {
    top: 50px;
    opacity: .25;
  }

  .slick-slide:nth-child(4n + 1) {
    .surf-box {
      margin-top: 32px;
    }
  }

  .slick-slide:nth-child(4n + 3) {
    .surf-box {
      margin-top: 89px;
    }
  }

  .slick-current {
    .surf-box__inner-btn {
      display: block;
    }

    .surf-box__inner {
      background: rgba(#1C2121, .3);
    }

  }
}

.surf-box{
  &__wrapper{
      background-position: center;
      background-repeat: no-repeat;
      background-size: cover;
  }
 &__inner{
  min-height: 550px;
  background: rgba(#1C2121, .85);
  position: relative;
  transition: all .5s;
      &:hover{
          background: rgba(#1C2121, .3);
          .surf-box__inner-btn{
              display: block;
          }
      }
  }
}

.surf-box__inner-title {
  font-weight: 800;
  font-size: 32px;
  line-height: 32px;
  padding: 72px 0 0 40px;
  width: 140px;
}

.surf-box__inner-location {
  font-weight: normal;
  font-size: 18px;
  line-height: 22px;

  letter-spacing: 8px;

  transform: rotate(-90deg);
  position: absolute;
  right: 40px;
  bottom: 15px;
  height: 0;
  width: 0;
  white-space: nowrap;

  span {
    font-weight: 100;
  }
}

.surf-box__inner-btn {
  bottom: 0;
  position: absolute;
  text-align: center;
  left: 0;
  right: 0;
  display: none;
}

.slider-btn{
  padding: 16px 26px;
  border: none;
  background-color: #fff;
  position: absolute;
  bottom: -48px;
  right: 0;
  cursor: pointer;
  z-index: 5;

  svg{
    stroke: #000;
  }
  &__left{
    right: 60px;
  }
  &.slick-disabled{
    background-color: #000;
    svg{
      stroke: #fff;
    }
  }
}

.benefits{


  &__items{
    display: flex;
    flex-wrap: wrap;
    padding: 80px;
  }
  &__subtitle{
    font-family: 'SFProDisplay-Regular',sans-serif;
    font-weight: 400;
    font-size: 36px;
    line-height: 53px;
    margin-top: 62px;
    margin-bottom: 0;
  }
  &__text{
    padding: 15px;
    border-radius: 10px;
    background-color: rgba(0, 0, 0, 0.5);
    font-size: 25px;
    line-height: 24px;
    font-weight: 300;
    font-family: 'SFProDisplay-Medium', sans-serif;

  }
}

.statistic-advantages {
  padding-bottom: 80px;
}

.advantages-list {
  display: flex;
  justify-content: space-around;
  align-items: center;
  text-align: center;
}

.advantages-list__item-text {
  font-weight: bold;
  font-size: 22px;
  line-height: 30px;
  margin-top: 32px;

}

