.container {
  max-width: 1230px;
  margin: 0 auto;
  padding: 0 15px;
}

.cars__subtitle {
  margin-left: 120px;
}


.cars_inner {
  display: flex;
}

.cars__images {
  margin-right: 90px;
  width: 500px;
  height: 700px;
}

.cars__text {
  font-weight: 800;
    font-size: 32px;
    line-height: 32px;
  margin-bottom: 40px;
}


.cars__gallery {
  max-width: 1000px;
  margin-top: 30px;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

.cars__gallery a {
  margin-top: 50px;
  margin-right: 10px;
}




// ...


.reviews-block {
  position: relative;
  padding:60px 0 40px;

  &__text{
    @extend %sf-regular;
    font-size: 36px;
    line-height: 53px;
    padding-left: 25px;
    margin-bottom: 55px;
  }
}

.review {
  margin: 0 auto;
  padding: 20px;
  width: 100%;
  max-width: 600px;
  flex: 0 0 auto;
  text-align:center
}

.image-cover {
  display: inline-block !important;
  border-radius: 50%;
  position: relative;
  width: 180px;
  height: 150px;
  overflow: hidden;
  box-shadow:0 0 5px 0 #fff;
  margin-bottom: 10px;
}

.reviews-carousel .review .review-text {
  background-color: #dca852;
  padding: 20px;
  border-radius: 6px;
  color: #fff;
  font-weight: 300;
  font-style:italic
}

.reviews-carousel .review .review-text h3 {
  text-align: left;
  font-weight: 400;
  margin-bottom: 10px;
  font-size: 20px;
  font-style:normal
}

.reviews-carousel .review .review-text p {
  text-align:justify
}

.reviews-carousel .review .name {
  font-size: 20px;
  color: #fff;
  font-style: italic;
  text-align: right;
  padding:10px 20px 5px 0
}

.review.category-1 .review-text {
  background-color: rgb(194, 154, 89);
}

.slick-btn {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  z-index: 5;
  cursor: pointer;
  background-color: transparent;
  border: none;
  outline: none;
}

.slick-prev {
  left: -75px;
}

.slick-next {
  right: -75px;
}


.contacts{
  &__inner{
    display: flex;
    padding: 25px;

    flex-direction: column;
    background-color: rgba(0, 0, 0, 0.5);
    width: 50%;
    border-radius: 10px;
  }
  &__info{
    width: 240px;
    margin-right: 60px;
  }
  &__info-box{
    margin-bottom: 30px;
  }
  &__info-title{
    font-size: 21px;
    line-height: 24px;
    @extend %ubuntu-bold;
    margin: 0 0 13px;
  }
  &__info-phone,
  &__info-email{
    font-size: 15px;
    line-height: 18px;
  }
  &__info-email:hover{
    text-decoration: underline;
  }
  &__form{
    width: 570px;
    margin-right: 106px;
  }
  &__form-title{
    font-size: 21px;
    line-height: 24px;
    @extend %ubuntu-bold;
    margin: 0 0 13px;
  }
  &__form-text{
    font-size: 15px;
    line-height: 18px;
    margin: 0 0 12px;
  }
  &__form-line{
    display: flex;
    justify-content: space-between;
    margin-bottom: 10px;
  }
  &__form-input,
  &__form-textarea{
    border: none;
    background: rgba(0, 0, 0, 0.5);
    font-size: 13px;
    line-height: 15px;
    @extend %ubuntu-regular;
    padding: 15px;
    color: #fff;
    &::placeholder{
      color: rgba(#fff, .4);
    }
  }

  &__form-input{
    width: 49%;
    background: rgba(0, 0, 0, 0.5);
  }

  &__form-textarea{
    min-height: 144px;
    width: 100%;
    resize: none;
    margin-bottom: 20px;
  }
  &__form-btn{
    text-transform: uppercase;
    font-size: 14px;
    line-height: 16px;
    letter-spacing: 0.03em;
    @extend %ubuntu-bold;
    border: 1px solid #FFFFFF;
    border-radius: 2px;
    padding: 9px 15px;
    color: #fff;
    background-color: transparent;
    cursor: pointer;
    transition: all .3s;
    &:hover {
      background-color: #fff;
      color: #1C1C1C;
    }
  }
  &__social-list{
    display: flex;
    flex-direction: column;
    justify-content: center;
  }
  &__social-item{
    margin: 14px 0;
  }
  &__social-link{
    opacity: 0.5;
    &:hover{
      opacity: 1;
    }
  }
}

.footer{
  position: absolute;
  bottom: 32px;
  left: 0;
  right: 0;
  &__copy{
    text-align: right;
    font-size: 13px;
    line-height: 15;
    color: rgba(#fff, .64);
  }
}

@import "media";
